.frontpage {
    padding: 20px;
    color: white !important;
    text-align: start;
}
.interface-container {
    text-align: start;
}

.success-container {
    position: absolute;
    top: 0;
    margin: auto;
    left: 0%;
    right: 0%;
    width: fit-content;
}

.hostname-container {
    display: flex;
    align-content: center;
    justify-content: space-between;
}
.sys-info {
    display: flex;
    align-content: center;
    justify-content: space-between;
    margin: 10px 0px;
}

.footer {
    margin-top: 30px;
    opacity: .8;
}

.banner {
    width: 100%;
    display: flex;
    align-content: center;
    justify-content: center;
    opacity: .8;
}